* { 
margin:0;
padding: 0;
font-family: 'poppins', sans-serif;
box-sizing: border-box;
}

/* body{
    background: #eef8ff;
} */

footer{
    width: 100%;
    /* position: absolute; */
    bottom:0;
    background: linear-gradient(to right, #00093c, #2d0b00);
    color: #fff;
    padding: 50px 0 30px;
    border-top-left-radius:125px;
    font-size: 13px;
    line-height: 20px;
}
.row{
    width: 85%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
}
.col{
   flex-basis: 25%;
   padding: 10px;
}
.co1:nth-child(2), .col:nth-child(3){
    flex-basis: 15%;
}

.logo1{
    width: 80px;
    margin-bottom: 30px;
    -webkit-filter: grayscale(1) invert(1);
    filter: grayscale(1) invert(1);
}
.col h3{
    width: fit-content;
    margin-bottom: 40px;
    position: relative;
}

.email-id{
    width: fit-content;
    border-bottom: 1px solid #ccc;
    margin: 20px 0;
}

ul li{
    list-style: none;
   margin-bottom: 12px;
}
ul li a{
    text-decoration: none;
    color: #fff;
}

 .form2{
    padding-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    margin-bottom: 50px;
}

.form2 .far{
    font-size: 18px;
   margin-right: 10px;
}
.form2 input{
   width: 100%;
    background: transparent;
    color: #ccc;
    border:0;
   outline: none;
}
.form2 button{
    background: transparent;
    border:0;
    outline: none;
    cursor: pointer;
 }

 .form2 button .fas{
    font-size: 16px;
    color: #ccc;
 } 

 .social-icons .fa {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    font-size: 20px;
    color: #fff;
    margin-right: 15px;
    cursor: pointer;
 }
 hr{
    width: 90%;
    border:0;
    border-bottom: 1px solid #ccc;
    margin: 20px auto;
 }
.copyright{
   text-align: center;
}

.underline2{
    width: 100%;
    height: 5px;
    background: #767676;
    border-radius: 3px;
    position: absolute;
    top: 25px;
    left: 0;
    overflow: hidden;
}

.underline2 span {
    width: 15px;
    height: 100%;
    background: #fff;
    border-radius: 3px;
    position: absolute;
    top: 0;
    left: 10px;
    animation: moving 2s linear infinite;
}

@keyframes moving {
    0% {
        left: -20px;
    }
    100% {
        left: calc(100% + 20px);
    }
}


@media (max-width: 700px){
    footer{
        bottom: unset;
    }
    .col{
    flex-basis: 100%;
    }
  .col:nth-child(2), .col:nth-child(3){
    flex-basis: 100%;
  }
}